.title {
  position: relative;

  width: 100%;
  margin: 0;

  text-align: center;
  color: $color-default-white;

  &--h1 {
    font-weight: 800;
    font-size: 46px;
    line-height: 69px;

    @include vp-1023 {
      font-size: 36px;
      line-height: 54px;
    }

    @include vp-767 {
      font-size: 22px;
      line-height: 33px;
    }
  }

  &--h2 {
    margin: 0 auto;

    font-weight: 800;
    font-size: 46px;
    line-height: 56px;

    @include vp-1023 {
      font-size: 36px;
      line-height: 46px;
    }

    @include vp-767 {
      font-size: 22px;
      line-height: 33px;
    }
  }

  &--h3 {
    margin: 0 auto;

    font-weight: 800;
    font-size: 36px;
    line-height: 49px;

    @include vp-1279 {
      margin-bottom: 32px;
    }

    @include vp-1023 {
      font-size: 28px;
      line-height: 42px;
    }

    @include vp-767 {
      margin: 0 0 12px;

      font-size: 18px;
      line-height: 25px;
    }
  }

  &--h4 {
    font-weight: 700;
    font-size: 20px;
    line-height: 30px;

    @include vp-1279 {
      font-size: 18px;
      line-height: 28px;
    }

    @include vp-1023 {
      font-size: 16px;
      line-height: 24px;
    }

    @include vp-767 {
      font-size: 14px;
      line-height: 21px;
    }
  }

  &--footer {
    font-weight: 700;
    font-size: 14px;
    line-height: 21px;
    text-align: left;

    @include vp-767 {
      text-align: center;
    }
  }
}
