.questions {
  &__block {
    position: relative;
    z-index: 1;
    margin-bottom: 25px;

    @include vp-1279 {
      margin-bottom: 21px;
    }

    @include vp-1023 {
      margin-bottom: 27px;
    }

    @include vp-619 {
      margin-bottom: 10px;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__highlighted-item {
    background: linear-gradient(263.99deg, $color-razzle-dazzle-rose 39.42%, $color-maya-blue 90.51%),
      linear-gradient(85.73deg, $color-supernova -25.39%, $color-medium-orchid 114.43%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  &__question {
    position: relative;
    width: 100%;
    max-width: 760px;
    margin: 0;
    // margin-bottom: 40px;
    padding: 29px 45px 29px 41px;
    background-color: $color-resolution-blue2;
    border-radius: 30px;

    font-weight: 800;
    font-size: 24px;
    line-height: 36px;

    @include vp-1023 {
      max-width: 580px;
      font-size: 18px;
      line-height: 30px;
      padding-right: 37px;
    }

    @include vp-619 {
      max-width: 260px;
    }

    @include vp-767 {
      max-width: 440px;
      font-size: 14px;
      line-height: 20px;
      padding: 15px 20px;
      border-radius: 20px;
    }
  }

  &__text {
    margin: 0;
  }

  &__svg {
    position: relative;
    color: $color-resolution-blue2;
    padding-bottom: 40px;

    @include vp-1023 {
      padding-bottom: 36px;
    }

    @include vp-619 {
      padding-bottom: 15px;
    }

    svg {
      position: absolute;
      width: 27px;
      height: 27px;
      top: -2px;
      left: 40px;
      object-fit: cover;

      @include vp-767 {
        width: 23px;
        height: 21px;
        left: 19px;
        // top: -17px;
      }
    }
  }

  &__svg--answers {
    position: relative;
    color: $color-cerulean-blue;

    svg {
      position: absolute;
      width: 27px;
      height: 27px;
      top: -2px;
      right: 42px;
      left: auto;
      object-fit: cover;
      transform: matrix(-1, 0, 0, 1, 0, 0);

      @include vp-767 {
        width: 23px;
        height: 21px;
        left: auto;
        right: 24px;
        top: -5px;
      }
    }
  }

  &__answers {
    position: relative;
    display: grid;
    grid-gap: 45px;
    grid-template-columns: repeat(4, 1fr);
    width: 100%;
    max-width: 1030px;
    margin: 0;
    padding: 0;
    margin-left: auto;
    padding: 44px 50px 50px 45px;
    list-style: none;
    background-color: $color-cerulean-blue;
    border-radius: 30px;

    .title--h4 {
      text-align: left;
    }

    @include vp-1279 {
      max-width: 740px;
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 32px 40px;
      padding-top: 33px;
      padding-bottom: 37px;
    }

    @include vp-1023 {
      max-width: 580px;
      grid-gap: 25px 29px;
      padding-left: 35px;
      padding-right: 34px;
      padding-bottom: 59px;
    }

    @include vp-619 {
      max-width: 260px;
    }

    @include vp-767 {
      max-width: 440px;
      grid-template-columns: repeat(1, 1fr);
      grid-gap: 10px;
      padding: 14px 20px 11px;
      border-radius: 20px;
    }
  }

  .card-item {
    padding: 0;

    svg {
      @include vp-767 {
        width: 37px;
        height: 28px;
      }
    }

    &--crypto svg {
      @include vp-1279 {
        height: 40px;
      }
    }

    // &--hands svg {
    //   @include vp-767 {
    //     width: 37px;
    //     height: 30px;
    //   }
    // }
  }

  .card-item svg {
    // width: 49px;
    // height: 40px;
    margin-bottom: 1px;
    object-fit: cover;
  }

  .title--h4 {
    margin-bottom: 7px;

    @include vp-1279 {
      max-width: 190px;
      font-size: 20px;
      margin-bottom: 11px;
    }

    @include vp-1023 {
      max-width: 215px;
      margin-bottom: 1px;
      font-size: 16px;
      line-height: 25px;
    }

    @include vp-767 {
      max-width: 185px;
      font-size: 14px;
      line-height: 22px;
    }
  }

  .card-item__text {
    font-weight: 500;
    font-size: 14px;
    line-height: 25px;
    opacity: 1;

    @include vp-767 {
      font-size: 12px;
      line-height: 20px;
    }
  }

  &__answers:last-child {
    .card-item svg {
      width: 40px;
      height: 40px;
    }
  }
}
