.promo {
  margin-bottom: 104px;

  @include vp-1023 {
    margin-bottom: 62px;
  }

  @include vp-767 {
    margin-bottom: 30px;
  }

  br {
    display: none;
  }

  @include vp-1439 {
    margin-bottom: 88px;
  }

  @include vp-1279 {
    margin-bottom: 70px;
  }

  &__title {
    margin-bottom: 46px;

    @include vp-1023 {
      margin-bottom: 31px;
      line-height: 54px;
    }

    @include vp-767 {
      margin-bottom: 12px;
      line-height: 30px;
      text-align: center;

      br {
        display: block;
      }
    }
  }

  &__title-gradient {
    background: linear-gradient(257.77deg, #ff45c8 -25.5%, #74bcff 101.84%), linear-gradient(85.33deg, #fcb42f -23.94%, #cb34e3 138.18%), linear-gradient(0deg, #ffffff, #ffffff);
    background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  &__list {
    margin: 0 0 6px;
    padding: 0;

    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    list-style: none;

    @include vp-1279 {
      margin: 0 0 16px;
    }

    @include vp-767 {
      margin: 0;
      grid-template-columns: 1fr;
      gap: 5px;
    }
  }

  &__list .card--promo {
    padding: 40px;

    background: linear-gradient(180deg, rgba(255, 255, 255, 0.1) 0%, rgba(242, 245, 249, 0) 100%);
    border-radius: 30px;

    @include vp-1279 {
      padding: 25px 29px;
    }

    @include vp-1023 {
      padding: 20px 25px;
      border-radius: 20px;
    }

    @include vp-767 {
      padding: 20px;
    }
  }

  &__list .card__title {
    margin: 0 0 14px;
    padding: 0;

    font-weight: 800;
    font-size: 22px;
    line-height: 33px;

    background: linear-gradient(257.77deg, #ff45c8 -25.5%, #74bcff 101.84%), linear-gradient(85.33deg, #fcb42f -23.94%, #cb34e3 138.18%), linear-gradient(0deg, #ffffff, #ffffff);
    background-clip: text;
    -webkit-text-fill-color: transparent;

    @include vp-1279 {
      margin: 0 0 9px;
      font-size: 18px;
      line-height: 27px;
    }

    @include vp-1023 {
      font-size: 16px;
      line-height: 24px;
    }

    @include vp-767 {
      margin: 0 0 5px;
      font-size: 14px;
      line-height: 21px;
    }
  }

  &__list .card__text {
    margin: 0;
    padding: 0;

    font-weight: 500;
    font-size: 16px;
    line-height: 28px;

    @include vp-1023 {
      font-size: 14px;
      line-height: 24px;
    }

    @include vp-767 {
      font-size: 12px;
      line-height: 20px;
    }
  }
}
