.main-screen {
  position: relative;
  padding: 98px 0 15px;

  @include vp-1279 {
    padding: 58px 0 15px;
  }

  @include vp-1023 {
    padding: 66px 0 0;
  }

  @include vp-767 {
    padding: 21px 0 0;
  }
}

.main-screen__background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  max-width: 2560px;
  height: 700px;
  object-fit: cover;
  animation: show 4s forwards;
  opacity: 0;

  @include vp-1279 {
    top: -40px;
  }

  @include vp-767 {
    top: 0;
    height: 410px;
  }
}

.main-screen img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.main-screen .container {
  position: relative;

  @include vp-1023 {
    padding-bottom: 32px;
  }

  @include vp-767 {
    padding-bottom: 7px;
  }
}

.main-screen__container {
  min-height: 278px;

  @include vp-1023 {
    min-height: 272px;
  }

  @include vp-767 {
    min-height: 171px;
  }
}

.main-screen__title {
  display: flex;
  flex-direction: column;

  @include vp-767 {
    padding-left: 1px;
  }
}

.main-screen__title span {
  font-weight: 700;
  font-size: 20px;
  line-height: 30px;
  margin-top: -4px;
  letter-spacing: -0.03px;

  @include vp-1023 {
    margin-top: 3px;
    font-size: 18px;
    line-height: 27px;
  }

  @include vp-767 {
    margin-top: 1px;
    margin-left: -2px;
    font-weight: 600;
    font-size: 14px;
    line-height: 21px;
  }
}

.main-screen__description {
  max-width: 809px;
  margin: 23px auto 10px;
  font-weight: 800;
  font-size: 32px;
  line-height: 46px;
  text-align: center;
  background: $promo-gradient;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  opacity: 0;
  animation: show 2s forwards;

  @include vp-1023 {
    max-width: 640px;
    margin: 26px auto 90px;
    font-size: 24px;
    line-height: 36px;
  }

  @include vp-767 {
    margin: 15px auto 29px;
    padding: 0 15px;
    font-size: 16px;
    line-height: 24px;
  }
}

.main-screen__wrapper {
  display: flex;
  justify-content: space-between;
  max-width: 780px;
  margin: 0 auto 145px;
  padding: 0 20px 0 87px;
  font-weight: 600;
  font-size: 20px;
  line-height: 30px;
  animation: show 4s forwards;
  opacity: 0;

  @include vp-1023 {
    max-width: 613px;
    margin: 0 auto 146px;
    padding: 0 0 0 46px;
    letter-spacing: -0.02px;
    font-size: 18px;
    line-height: 27px;
  }

  @include vp-767 {
    margin: 0 auto 76px;
    font-size: 12px;
    line-height: 18px;
    padding: 0 1px 0 36px;
  }
}

.main-screen__wrapper span {
  max-width: 300px;

  @include vp-1023 {
    max-width: 230px;
  }

  @include vp-767 {
    max-width: 85px;
  }
}
