.media__list {
  display: flex;
  position: absolute;
  left: 0;
  padding: 0;
  margin: 0;
  list-style: none;
  justify-content: center;

  &--top {
    bottom: 331px;
    transform: translateX(-205px);
    animation: slider--desktop 34s linear infinite;
    z-index: 10;

    @include vp-1279 {
      bottom: 286px;
      transform: translateX(-242px);
      animation: slider--tablet 34s linear infinite;
    }

    @include vp-1023 {
      bottom: 240px;
      transform: translateX(-191px);
      animation: slider--mobile 34s linear infinite;
    }

    @include vp-767 {
      position: relative;
      bottom: auto;
      max-width: 450px;
      width: 100%;
      margin: 0 auto;
      flex-direction: column;
      align-items: center;
      transform: translate(0);
      animation: slider--mobile 0s linear infinite;
    }
  }

  &--bottom {
    bottom: 28px;
    transform: translateX(-96px);
    animation: slider--desktop 34s linear infinite;

    @include vp-1279 {
      bottom: 20px;
      transform: translateX(-150px);
      animation: slider--tablet 34s linear infinite;
    }

    @include vp-1023 {
      bottom: 28px;
      transform: translateX(-119px);
      animation: slider--mobile 34s linear infinite;
    }

    @include vp-767 {
      display: none;
    }
  }
}

.media__list--top .slider__item {
  margin-right: 50px;

  @include vp-1279 {
    margin-right: 41px;
  }

  @include vp-1023 {
