.cases {
  margin-bottom: 104px;
  padding: 84px 0 96px;

  background-color: $color-sapphire-4;

  @include vp-1439 {
    margin-bottom: 76px;
    padding: 56px 0 65px;
  }

  @include vp-1279 {
    margin-bottom: 68px;
    padding: 60px 0 65px;
  }

  @include vp-1023 {
    margin-bottom: 59px;
    padding: 43px 0 57px;
  }

  @include vp-767 {
    margin-bottom: 20px;
    padding: 21px 0 51px;
  }

  &__title {
    margin-bottom: 31px;

    font-weight: 800;
    font-size: 46px;
    line-height: 69px;

    @include vp-1023 {
      margin-bottom: 23px;

      font-size: 36px;
      line-height: 54px;
    }

    @include vp-767 {
      margin-bottom: 12px;

      font-size: 22px;
      line-height: 33px;
      text-align: left;
    }
  }

  &__list {
    margin: 0;
    padding: 0;

    display: grid;
    grid-template-columns: repeat(3, 1fr);

    grid-template-rows: repeat(auto-fit, auto);

    gap: 0 20px;

    list-style: none;

    @include vp-767 {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  &__item {
    position: relative;

    min-height: 421px;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;

    border-radius: 30px;

    @include vp-1279 {
      min-height: 339px;
    }

    @include vp-1023 {
      min-height: 243px;
    }

    @include vp-767 {
      min-height: 274px;
    }
  }

  &__item--2,
  &__item--3 {
    @include vp-767 {
      display: none;
    }
  }

  &__img {
    position: relative;

    width: 100%;
    min-height: 250px;
    margin-bottom: 31px;

    border-radius: 30px;

    @include vp-1279 {
      min-height: 178px;
      margin-bottom: 25px;
    }

    @include vp-1023 {
      min-height: 140px;
      margin-bottom: 13px;
    }

    @include vp-767 {

      height: 100%;

      margin-bottom: 10px;
    }
  }

  &__img-bg {
    width: 100%;
    height: 100%;

    object-fit: cover;
    border-radius: 30px;
  }

  &__img img:last-of-type {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;

    width: 100%;
    height: 100%;

    object-fit: cover;
  }

  &__img--souz img:last-of-type {
    width: 84%;
    height: 73%;

    object-fit: contain;
  }


  &__info {
    padding: 0 20px 20px;

    @include vp-1023 {
      padding: 0 10px 10px;
    }

    @include vp-767 {
      padding: 0;
    }
  }

  &__info-title {
    margin-bottom: 10px;

    font-weight: 800;
    font-size: 22px;
    line-height: 26px;

    text-align: left;

    @include vp-1279 {
      font-size: 20px;
    }

    @include vp-1023 {
      margin-bottom: 4px;

      font-size: 16px;
    }
  }

  &__info-text {
    margin: 0;
    padding: 0;

    font-weight: 500;
    font-size: 16px;
    line-height: 26px;

    opacity: 0.6;

    @include vp-1279 {
      font-size: 14px;
      line-height: 25px;
    }

    @include vp-1023 {
      font-size: 12px;
      line-height: 20px;
    }
  }
}
