.contacts {
  max-width: 330px;
  margin: 0 auto;

  @include vp-1279 {
    max-width: 250px;
    margin: 0 auto 0 0;
  }

  @include vp-1023 {
    max-width: 210px;
    margin: 0;
  }

  &__content {
    margin-top: 12px;

    @include vp-1279 {
      margin-top: 8px;
    }

    @include vp-767 {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }

  &__info {
    margin: 0;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;

    font-family: $ff-poppins;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;

    @include vp-1023 {
      font-size: 12px;
      line-height: 18px;
    }
  }

  &__info-note,
  &__info-link {
    color: $color-default-white;
    opacity: 0.6;
  }

  &__info-note {
    margin-right: 5px;
  }

  &__info-link {
    font-weight: 500;
    opacity: 1;

    transition: opacity $trans-default;

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        color: $color-default-white;
        opacity: 0.7;
      }
    }
  }

  &__info--address .contacts__info-link {
    opacity: 0.8;
  }
}
