.custom-input,
.custom-textarea {
  position: relative;

  display: flex;
  // padding: 20px 0;

  label {
    display: flex;
    // align-items: center;
    flex-direction: column;
    width: 100%;

    input,
    textarea {
      position: relative;
      flex-grow: 1;
      min-height: 50px;
      // margin-left: 30px;
      padding: 10px;

      border: 1px solid rgba(0, 26, 84, 0.2);
      border-radius: 10px;
      outline: none;

      transition: border $trans-default;

      // &:hover {
      //   border: 2px solid rgba($color-default-black, 0.5);
      // }

      &:focus {
        border: 1px solid $color-sapphire;
        color: $color-sapphire;
      }
    }
  }

  &__error {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    right: 0;
    bottom: -22px;
    width: 100%;
    opacity: 0;

    transition: opacity $trans-default;

    &-description {
      position: absolute;
      right: calc(-50% + 27px);
      text-align: center;
      top: calc(100% - 28px);
      width: 100%;
      z-index: 1;
      opacity: 0;

      @include vp-767 {
        display: flex;
        justify-content: flex-end;
        right: 0;
        left: 0;
      }

      &::before {
        color: $color-default-white;
        border: 10px solid transparent;
        border-top: 10px solid $color-pumpkin;
        content: "";
        left: calc(50% - 6px);
        position: absolute;
        top: -16px;
        transform: rotate(180deg);

        @include vp-767 {
          left: calc(100% - 37px);
        }
      }
    }

    &-text {
      display: inline-flex;
      margin: 0;
      max-width: 200px;
      text-align: center;
      background: $color-pumpkin;
      padding: 4px 10px;
      border-radius: 10px;
      color: $color-default-white;
      font-size: 10px;
      line-height: 14px;
    }
  }

  &.is-invalid {
    textarea,
    input {
      border: 1px solid $color-pumpkin;

      &:focus,
      &:hover {
        border: 1px solid $color-pumpkin;
      }
    }

    .custom-input__error {
      opacity: 1;

      &-svg {
        border-radius: 50%;
        bottom: 37px;

        display: inline-flex;
        justify-content: center;
        align-items: center;
        height: 20px;
        position: absolute;
        right: 13px;
        width: 20px;
        background-color: $color-pumpkin;
        cursor: pointer;

        @include vp-767 {
          right: 17px;
          bottom: 35px;
        }
      }
    }

    .custom-textarea__error {
      opacity: 1;


      @include vp-767 {
        bottom: -61px;
      }


      &-svg {
        border-radius: 50%;
        bottom: 138px;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        height: 20px;
        position: absolute;
        right: 13px;
        width: 20px;
        background-color: $color-pumpkin;
        cursor: pointer;


        @include vp-767 {
          right: 17px;
        }
      }
    }
  }
  /*&.is-valid {
    input {
      border: 2px solid $color-malachite;

      &:focus,
      &:hover {
        border: 2px solid rgba($color-malachite, 0.5);
      }
    }
  }*/
}

.custom-textarea {
  position: relative;

  display: flex;

  label {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  &__error {
    &-description {
      position: absolute;
      right: calc(-50% + 27px);
      text-align: center;
      top: calc(100% - 130px);
      width: 100%;
      z-index: 1;
      opacity: 0;

      @include vp-767 {

        display: flex;
        justify-content: flex-end;
        right: 0;
        left: 0;
      }

      &::before {
        color: $color-default-white;
        border: 10px solid transparent;
        border-top: 10px solid $color-pumpkin;
        content: "";

        left: calc(50% - 6px);

        position: absolute;
        top: -16px;
        transform: rotate(180deg);

        @include vp-767 {
          left: calc(100% - 37px);
        }
      }
    }
  }
}

.custom-input__error-svg:hover + .custom-input__error-description {
  opacity: 1;
}

.custom-textarea__error-svg:hover + .custom-textarea__error-description {
  opacity: 1;
}
