.meta-emergence {
  margin-bottom: 60px;

  @include vp-1279 {
    margin-bottom: 55px;
  }

  @include vp-1023 {
    margin-bottom: 33px;
  }

  @include vp-767 {
    margin-bottom: 19px;
  }

  &__title {
    margin-bottom: 26px;
    font-weight: 700;
    line-height: 46px;

    @include vp-1023 {
      margin-bottom: 23px;
    }

    @include vp-767 {
      margin-bottom: 11px;
      line-height: 27px;

      text-align: left;
    }
  }

  &__list {
    margin: 0;
    padding: 0;

    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: auto;
    justify-content: center;
    gap: 20px;

    list-style: none;

    @include vp-767 {
      grid-template-columns: auto;
      gap: 10px 0;
    }
  }

  &__list .card-item {
    min-height: 235px;
    padding: 40px;

    font-weight: 500;
    font-size: 16px;
    line-height: 29px;

    background: linear-gradient(145.47deg, rgba(255, 255, 255, 0.1) -30.49%, rgba(255, 255, 255, 0) 155.15%);
    border-radius: 30px;

    @include vp-1279 {
      min-height: 270px;
      padding: 30px 50px 30px 30px;
    }

    @include vp-1023 {
      min-height: 250px;
      padding: 30px 20px 30px 30px;

      font-size: 14px;
      line-height: 25px;
    }

    @include vp-767 {
      min-height: 100px;
      padding: 15px 30px 15px 20px;

      display: grid;
      grid-template-columns: 44px 1fr;
      gap: 0 15px;
      align-items: center;

      font-size: 12px;
      line-height: 20px;
    }

    svg {
      width: 54px;
      height: 54px;
      margin-bottom: 3px;

      @include vp-767 {
        width: 44px;
        height: 44px;
        margin: 0;
      }
    }
  }
}
