.meta-companies {
  position: relative;
  padding-bottom: 127px;

  @include vp-1439 {
    padding-bottom: 100px;
  }

  @include vp-1023 {
    padding-bottom: 71px;
  }

  @include vp-767 {
    padding-bottom: 17px;
  }

  img {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;

    width: 100%;
    max-width: 1440px;
    min-height: 154px;
    margin: 0 auto;

    object-fit: cover;


    @include vp-1279 {
      min-height: 154px;
    }

    @include vp-767 {
      max-width: 1100px;
      min-height: 117px;
    }
  }

  &__text {
    margin: 0 auto 20px;
    padding: 0;

    font-weight: 600;
    font-size: 28px;
    line-height: 46px;

    text-align: center;

    @include vp-1279 {
      margin: 0 auto 23px;
    }

    @include vp-1023 {
      font-size: 20px;
      line-height: 46px;
    }

    @include vp-767 {
      margin: 0 0 8px;
      padding-right: 20px;

      font-weight: 700;
      font-size: 16px;
      line-height: 24px;

      text-align: left;
    }
  }
}

.meta-companies__list {
  position: relative;
  z-index: 10;

  max-width: 1180px;
  margin: 0;
  padding: 50px 55px;

  display: grid;
  grid-template-columns: repeat(6, auto);
  gap: 20px;

  background-color: rgba(255, 255, 255, 0.06);
  border-radius: 30px;
  list-style: none;

  @include vp-1279 {
    padding: 41px 40px 46px;
  }

  @include vp-1023 {
    padding: 33px 37px;
  }

  @include vp-767 {
    margin: 0;
    padding: 23px 10px;
    gap: 15px;
  }

  @include vp-479 {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  .card-item {
    padding: 0;

    display: flex;
    align-items: center;
    justify-content: center;

    border-radius: 0;
    opacity: 0.5;
  }

  .card-item--apple {
    svg {
      width: 42px;
      height: 51px;

      @include vp-1279 {
        width: 36px;
        height: 44px;
      }

      @include vp-1023 {
        width: 28px;
        height: 34px;
      }

      @include vp-767 {
        width: 22px;
        height: 26px;
      }
    }
  }

  .card-item--meta {
    svg {
      width: 125px;
      height: 25px;

      @include vp-1279 {
        width: 105px;
        height: 21px;
      }

      @include vp-1023 {
        width: 81px;
        height: 16px;
      }

      @include vp-767 {
        width: 62px;
        height: 12px;
      }
    }
  }

  .card-item--google {
    svg {
      width: 125px;
      height: 41px;

      @include vp-1279 {
        width: 105px;
        height: 34px;
      }

      @include vp-1023 {
        width: 81px;
        height: 26px;
      }

      @include vp-767 {
        width: 62px;
        height: 20px;
      }
    }
  }

  .card-item--disney {
    svg {
      width: 117px;
      height: 49px;

      @include vp-1279 {
        width: 99px;
        height: 41px;
      }

      @include vp-1023 {
        width: 76px;
        height: 31px;
      }

      @include vp-767 {
        width: 58px;
        height: 23px;
      }
    }
  }

  .card-item--roblox {
    svg {
      width: 136px;
      height: 23px;

      @include vp-1279 {
        width: 115px;
        height: 19px;
      }

      @include vp-1023 {
        width: 89px;
        height: 14px;
      }

      @include vp-767 {
        width: 72px;
        height: 12px;
      }
    }
  }

  .card-item--microsoft {
    svg {
      width: 160px;
      height: 34px;

      @include vp-1279 {
        width: 135px;
        height: 28px;
      }

      @include vp-1023 {
        width: 104px;
        height: 21px;
      }

      @include vp-767 {
        width: 84px;
        height: 17px;
      }
    }
  }
}
