.custom-toggle {
  position: relative;

  display: flex;
  // padding: 20px 0;

  &__error {
    position: absolute;
    bottom: 0;
    left: 0;

    color: $color-pumpkin;

    opacity: 0;

    transition: opacity $trans-default;
  }

  input {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;

    clip: rect(0 0 0 0);
  }

  label {
    position: relative;
    padding-left: 26px;

    cursor: pointer;

    span {
      margin-bottom: 0;
      margin-left: 0;
    }
  }

  &--checkbox {
    .custom-toggle__icon {
      position: absolute;
      top: 2px;
      left: 0;

      width: 18px;
      height: 18px;

      background: rgba(0, 26, 84, 0.1);
      border-radius: 5px;

      &:hover {
        background: rgba(0, 26, 84, 0.1);
      }

      // transition: background $trans-default;

      &::before {
        // content: "";
        // position: absolute;
        // top: 50%;
        // left: 50%;

        // width: 6px;
        // height: 6px;

        // background-color: $color-transparent;

        // transform: translate(-50%, -50%);

        // transition: background-color $trans-default;
      }
    }
  }

  // &--radio {
  //   .custom-toggle__icon {
  //     position: absolute;
  //     top: 2px;
  //     left: 0;

  //     width: 16px;
  //     height: 16px;

  //     border: 1px solid rgba($color-default-black, 0.2);
  //     border-radius: 50%;

  //     transition: border $trans-default;

  //     &::before {
  //       content: "";
  //       position: absolute;
  //       top: 50%;
  //       left: 50%;

  //       width: 6px;
  //       height: 6px;

  //       color: $color-transparent;

  //       border-radius: 50%;
  //       transform: translate(-50%, -50%);

  //       transition: color $trans-default;
  //     }
  //   }
  // }

  input:checked + .custom-toggle__icon {
    // position: relative;
    background: linear-gradient(140.17deg, #FFD300 -4.61%, #DF6C3B 102.74%);

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 10px;
      height: 8px;

      // background-color: rgba($color-default-black, 0.8);
      background-image: url("../img/svg/checked.svg");
      transition: color $trans-default;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: 0 0;
    }
  }

  &.is-invalid {
    .custom-toggle__icon {
      border: 1px solid $color-pumpkin;
    }

    .custom-toggle__error {
      opacity: 1;
    }
  }

  /*&.is-valid {
    label span {
      color: $color-eucalyptus;
    }
  }*/
}
