.nft-objects {
  &__title {
    margin-bottom: 5px;

    line-height: 54px;

    @include vp-1279 {
      margin-bottom: 2px;
    }

    @include vp-1023 {
      margin-bottom: 8px;
    }

    @include vp-767 {
      margin-bottom: 4px;

      line-height: 30px;

      text-align: left;
    }
  }

  &__text {
    max-width: 610px;
    margin: 0 auto 30px;

    font-weight: 600;
    font-size: 20px;
    line-height: 34px;

    text-align: center;

    @include vp-1023 {
      font-size: 18px;
      line-height: 30px;
    }

    @include vp-767 {
      margin: 0 0 15px;

      font-size: 14px;
      line-height: 24px;

      text-align: left;
    }
  }

  &__list {
    margin: 0 auto 58px;
    padding: 0;

    display: grid;
    grid-template-columns: repeat(auto-fit, 220px);
    gap: 20px;

    list-style: none;

    @include vp-1279 {
      margin: 0 auto 50px;
      grid-template-columns: repeat(auto-fit, 160px);
      gap: 10px;
      justify-content: center;
    }

    @include vp-1023 {
      margin: 0 auto 41px;
      grid-template-columns: repeat(auto-fit, 125px);
    }

    @include vp-767 {
      margin: 0 0 32px;
      grid-template-columns: repeat(auto-fit, 90px);
    }
  }

  &__list .card-item {
    position: relative;
    width: 220px;
    height: 184px;
    padding: 47px 30px 35px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background: linear-gradient(216.23deg, rgba(255, 255, 255, 0.1) -22.04%, rgba(255, 255, 255, 0) 138.47%);

    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: inherit;
      border: 1px solid transparent;
      mask: linear-gradient($color-default-white, $color-default-white) padding-box, linear-gradient($color-default-white, $color-default-white);
      mask-composite: exclude;
      background: linear-gradient(161.07deg, rgba(255, 255, 255, 0.5) -213.89%, rgba(255, 255, 255, 0) 103.64%) border-box;
    }

    @include vp-1279 {
      width: 160px;
      height: 165px;
      padding: 41px 20px 30px;
    }

    @include vp-1023 {
      width: 125px;
      height: 130px;
      padding: 35px 10px 24px;
    }

    @include vp-767 {
      width: 90px;
      height: 100px;
      padding: 20px 10px 13px;
    }

    h4 {
      font-weight: 600;
      font-size: 18px;
      line-height: 27px;
      text-align: center;

      @include vp-1279 {
        font-size: 16px;
      }

      @include vp-1023 {
        font-size: 14px;
        line-height: 21px;
      }

      @include vp-767 {
        font-size: 12px;
        line-height: 18px;
      }
    }

    svg {
      @include vp-1023 {
        width: 42px;
        height: 42px;
      }

      @include vp-767 {
        width: 36px;
        height: 36px;
      }
    }
  }

  &__example {
    margin: 0 auto;
    padding: 0 60px 0 37px;

    display: grid;
    grid-template-columns: auto 1fr;
    gap: 0 85px;
    align-items: center;

    @include vp-1279 {
      padding: 0 30px 0 0;
    }

    @include vp-1023 {
      padding: 0 15px 0 0;
      gap: 0 70px;
    }

    @include vp-767 {
      padding: 0;

      display: flex;
      flex-direction: column-reverse;
    }
  }

  &__img {
    max-width: 490px;
    min-height: 212px;
    padding: 14px 16px;

    background-color: $color-default-white;
    border-radius: 20px;

    @include vp-1279 {
      max-width: 416px;
      min-height: 180px;
    }

    @include vp-1023 {
      max-width: 326px;
      min-height: 140px;
      padding: 10px;
    }

    @include vp-767 {
      max-width: 100%;
      min-height: 130px;
      margin-right: 0;
    }

    img {
      width: 100%;
      height: 100%;

      object-fit: cover;
    }
  }

  &__date {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      top: 18px;
      left: -55px;

      width: 40px;
      height: 2px;

      background-color: $color-resolution-blue;


      @include vp-1023 {
        top: 13px;
        left: -42px;
        width: 30px;
      }

      @include vp-767 {
        top: -11px;
        left: 0;
      }
    }

    @include vp-767 {
      max-width: 100%;
      margin-bottom: 6px;
      padding-right: 12px;
    }

    p {
      margin: 0 0 13px;

      font-weight: 600;
      font-size: 24px;
      line-height: 36px;

      @include vp-1279 {
        margin: 0 0 22px;

        font-size: 20px;
        line-height: 30px;
      }

      @include vp-1023 {
        margin: 0;

        font-size: 16px;
        line-height: 24px;
      }

      @include vp-767 {
        font-size: 13px;
        line-height: 19px;
      }
    }

    span {
      font-weight: 500;
      font-style: italic;
      font-size: 16px;
      line-height: 24px;

      opacity: 0.6;

      @include vp-1023 {
        font-size: 14px;
        line-height: 21px;
      }

      @include vp-767 {
        font-size: 12px;
        line-height: 12px;
      }
    }
  }
}
