.conditions .container {
  margin: 70px auto 62px;

  @include vp-1279 {
    margin: 68px auto 62px;
  }

  @include vp-1023 {
    margin: 59px auto 15px;
  }

  @include vp-767 {
    margin: 25px auto 26px;
    padding: 0;
  }
}

.conditions__title {
  margin: 0 auto 42px;
  max-width: 721px;
  text-align: center;

  @include vp-1023 {
    margin: 0 auto 31px;
    max-width: 565px;
    font-size: 36px;
    line-height: 46px;
  }

  @include vp-767 {
    max-width: 100%;
    width: auto;
    margin: 0 15px 15px;
    text-align: start;
    font-size: 22px;
    line-height: 30px;
  }
}

.conditions__list {
  list-style: none;
  margin: 0 auto 60px;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;

  @include vp-1279 {
    gap: 18px;
    margin-bottom: 51px;
  }

  @include vp-1023 {
    gap: 19px;
    margin-bottom: 54px;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
    gap: 0;
    margin: 0 15px 13px;
  }
}

.conditions__list .card-item {
  background-color: $color-white-transparent2;
  border-radius: 30px;
  min-height: 270px;
  padding: 45px 50px 38px 46px;

  @include vp-1279 {
    min-height: auto;
    padding: 30px 30px 28px;
  }

  @include vp-1023 {
    padding: 29px 23px 33px 30px;
  }

  @include vp-767 {
    min-height: 150px;
    padding: 19px 20px 20px;
    border-radius: 20px;
    margin-bottom: 10px;
  }
}

.conditions__list .card-item--monetization {
  padding-top: 47px;

  @include vp-1279 {
    padding-top: 32px;
  }

  @include vp-1023 {
    padding-top: 31px;
  }

  @include vp-767 {
    padding-top: 20px;
  }
}

.conditions__list .card-item--lucidity {
  @include vp-1279 {
    padding-top: 29px;
  }
}

.card-item--contract svg {
  @include vp-1279 {
    width: 45px;
    height: 45px;
  }

  @include vp-1023 {
    width: 42px;
    height: 42px;
  }

  @include vp-767 {
    width: 36px;
    height: 36px;
  }
}

.card-item--monetization svg {
  @include vp-1279 {
    width: 53px;
    height: 41px;
  }

  @include vp-1023 {
    width: 50px;
    height: 38px;
  }

  @include vp-767 {
    width: 42px;
    height: 34px;
  }
}

.card-item--lucidity svg {
  @include vp-1279 {
    width: 44px;
    height: 46px;
  }

  @include vp-1023 {
    width: 41px;
    height: 42px;
  }

  @include vp-767 {
    width: 35px;
    height: 36px;
  }
}

.card-item--contract .title,
.card-item--monetization .title,
.card-item--lucidity .title {
  font-size: 24px;
  line-height: 34px;
  text-align: left;

  @include vp-1279 {
    font-size: 20px;
  }

  @include vp-1023 {
    font-size: 16px;
  }

  @include vp-767 {
    font-size: 14px;
    line-height: 21px;
  }
}

.card-item--contract .title {
  margin: 8px 0 9px;

  @include vp-1279 {
    margin: 2px 0 1px;
  }

  @include vp-1023 {
    margin: 3px 0 2px;
  }

  @include vp-767 {
    margin: 2px 0 4px;
  }
}

.card-item--monetization .title {
  margin: 11px 0 9px;

  @include vp-1279 {
    margin: 4px 0 1px;
  }

  @include vp-1023 {
    margin: 5px 0 2px;
  }

  @include vp-767 {
    margin: 3px 0 4px;
  }
}

.card-item--lucidity .title {
  margin: 9px 0 8px;

  @include vp-1279 {
    margin: 2px 0 1px;
  }

  @include vp-1023 {
    margin: 3px 0 2px;
  }

  @include vp-767 {
    margin: 2px 0 4px;
  }
}

.card-item--contract .card-item__text,
.card-item--monetization .card-item__text,
.card-item--lucidity .card-item__text {
  line-height: 26px;

  @include vp-1023 {
    font-size: 14px;
    line-height: 25px;
  }

  @include vp-767 {
    font-size: 12px;
    line-height: 20px;
  }
}

.card-item--monetization br {
  @include vp-767 {
    display: none;
  }
}


// -------------------- table ----------------------------


.cooperation {
  margin: 0 auto 68px;
  max-width: 1180px;

  @include vp-1279 {
    max-width: 846px;
    margin-bottom: 69px;
  }

  @include vp-1023 {
    max-width: 668px;
    margin-bottom: 0;
  }

  @include vp-767 {
    max-width: none;
    overflow: hidden;
    position: relative;

    &::after {
      content: "";
      position: absolute;
      top: 66px;
      right: 0;
      width: 35px;
      height: 32px;
      background: $sapphire-gradient;
    }
  }
}

.cooperation__title {
  margin-bottom: 40px;

  @include vp-1279 {
    margin-bottom: 22px;
  }

  @include vp-1023 {
    max-width: 274px;
    margin: 0;
    text-align: left;
    line-height: 36px;
  }

  @include vp-767 {
    font-size: 18px;
    line-height: 25px;
    margin: 0 15px 13px;
    padding-right: 100px;
  }
}

.cooperation__wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-end;

  @include vp-1023 {
    transform: translateY(-60px);
  }

  @include vp-767 {
    transform: translateY(0);
    align-items: flex-start;
    display: none;
  }
}

.table__description--title {
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  opacity: 0.65;
}

.table__description--text {
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
}

.table__description--data th,
.table__description--features th {
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;

  @include vp-767 {
    font-size: 12px;
    line-height: 18px;
  }
}

.description-title {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
}

.table__wrapper {
  display: grid;
  grid-template-columns: 32px auto;

  @include vp-767 {
    grid-template-columns: 25px auto;
  }
}

.data-wrapper,
.features-wrapper {
  padding: 10px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $color-brink-pink;
  border-radius: 0;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;

  @include vp-767 {
    border-radius: 0;
  }
}

.features-wrapper {
  background-color: $color-white-transparent4;
}

.table__container {
  display: flex;
  justify-content: end;

  @include vp-1023 {
    transform: translateY(-60px);
  }

  @include vp-767 {
    position: relative;
    overflow: auto;
    justify-content: start;
    transform: translateY(0);
    margin-bottom: 16px;

    &::after {
      content: "";
      position: absolute;
      bottom: 4px;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: $color-white-transparent5;
    }
  }
}

.cooperation__table {
  border: none;
  border-spacing: 0;
}

.data-title,
.features-title {
  margin: 0;
  padding-bottom: 2px;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: $color-white-transparent6;
  white-space: pre;
  writing-mode: vertical-lr;
  transform: rotate(-180deg);

  @include vp-1279 {
    padding-bottom: 1px;
    padding-left: 2px;
  }

  @include vp-1023 {
    padding-bottom: 0;
  }

  @include vp-767 {
    font-size: 12px;
    line-height: 18px;
    padding-left: 0;
    padding-right: 1px;
    padding-bottom: 40px;
  }
}

.features-title {
  @include vp-767 {
    padding-bottom: 59px;
  }
}

.cooperation__table--data {
  text-align: left;
  width: 100%;
  background-color: $color-brink-pink;
  border-radius: 0;
  border-top-right-radius: 15px;

  @include vp-767 {
    border-radius: 0;
  }
}

.cooperation__table--data tr {
  &:nth-child(2n+1) {
    background-color: $color-white-transparent3;
  }
}

.table__description--data:first-child td:last-child {
  border-top-right-radius: 15px;

  @include vp-767 {
    border-radius: 0;
  }
}

.table__description--features:last-child td:last-child {
  border-bottom-right-radius: 15px;

  @include vp-767 {
    border-radius: 0;
  }
}

.cooperation__table--data .table__description--title,
.cooperation__table--data .table__description--text {
  background-color: transparent;
}

.cooperation__table--features {
  text-align: left;
  width: 100%;
  background-color: $color-white-transparent4;
  border-radius: 0;
  border-bottom-right-radius: 15px;

  @include vp-767 {
    border-radius: 0;
  }
}

.cooperation__table--features tr:nth-child(2n+1) {
  background-color: $color-white-transparent3;
}

.cooperation__table svg {
  display: block;
}

.cooperation__table th,
.cooperation__table td {
  padding-left: 16px;
  border-left: 1px solid $color-white-transparent5;

  @include vp-767 {
    border-color: transparent;
    padding-left: 17px;
  }
}

.cooperation__table--data th,
.cooperation__table--features th {
  @include vp-767 {
    border-color: $color-white-transparent5;
  }
}

.cooperation__table th {
  width: 458px;

  @include vp-1279 {
    width: 429px;
  }

  @include vp-1023 {
    width: 349px;
  }

  @include vp-767 {
    width: 330px;
  }
}

.cooperation__table--title {
  @include vp-767 {
    width: auto;
    margin-bottom: 5px;
    padding-bottom: 2px;
    border-spacing: 17px 0;
  }
}

.cooperation__table--title th,
.cooperation__table td {
  width: 230px;
  min-width: 230px;

  @include vp-1279 {
    width: 131px;
    min-width: 131px;
  }

  @include vp-1023 {
    width: 101px;
    min-width: 101px;
  }

  @include vp-767 {
    width: 50px;
    min-width: auto;
  }
}

.cooperation__table--title th {
  vertical-align: baseline;
}

.cooperation__table--title th:last-child,
.cooperation__table td:last-child {
  @include vp-1279 {
    width: 123px;
    min-width: 123px;
  }

  @include vp-1023 {
    width: 85px;
    min-width: 85px;
  }

  @include vp-767 {
    width: 50px;
    min-width: auto;
  }
}

.table__description td:not(:nth-child(2)) {
  padding-left: 14px;

  @include vp-1279 {
    padding-left: 15px;
  }
}

.table__description td {
  @include vp-1279 {
    padding-left: 15px;
  }

  @include vp-767 {
    padding-left: 0;
    padding-right: 10px;
  }
}

.table__description th {
  @include vp-767 {
    padding-right: 47px;
  }
}

.description-title br {
  @include vp-767 {
    display: none;
  }
}

.cooperation__table--features td {
  @include vp-767 {
    padding-right: 13px;
  }
}

.table__description th,
.table__description td {
  @include vp-767 {
    padding-top: 8px;
    padding-bottom: 8px;
  }
}

.table__description--data1 th {
  padding-top: 11px;
  padding-bottom: 9px;

  @include vp-1023 {
    padding-top: 11px;
  }
}

.table__description--data2 th {
  padding-top: 11px;
  padding-bottom: 10px;

  @include vp-1023 {
    padding-bottom: 11px;
  }
}

.table__description--data3 th {
  padding-top: 9px;
  padding-bottom: 12px;

  @include vp-1023 {
    padding-top: 8px;
  }
}

.table__description--data4 th {
  padding-top: 9px;
  padding-bottom: 12px;

  @include vp-1023 {
    padding-bottom: 13px;
  }
}

.table__description--data1 td {
  padding-top: 11px;
  padding-bottom: 10px;

  @include vp-1023 {
    padding-bottom: 11px;
  }
}

.table__description--data2 td {
  padding-top: 12px;
  padding-bottom: 9px;

  @include vp-1023 {
    padding-top: 11px;
    padding-bottom: 10px;
  }
}

.table__description--data3 td {
  padding-top: 11px;
  padding-bottom: 10px;

  @include vp-1023 {
    padding-top: 10px;
  }
}

.table__description--data4 td {
  padding-top: 12px;
  padding-bottom: 9px;

  @include vp-1023 {
    padding-top: 11px;
  }
}


// -------------------table features------------------------------


.table__description--features1 th {
  padding-top: 12px;
