.cooperation__table .table__cell {
  padding: 0 14px 14px;

  @include vp-1279 {
    padding: 0 10px 10px 15px;
  }

  @include vp-1023 {
    padding-bottom: 7px;
    padding-right: 20px;
  }

  @include vp-767 {
    width: auto;
    padding: 0;
    transform: translateX(-7px);

    &--3 {
      padding-left: 1px;
    }
  }
}

.cooperation__table .table__cell:first-child {
  padding: 0 16px 14px;

  @include vp-1279 {
    padding: 0 15px 19px;
  }

  @include vp-767 {
    width: auto;
    padding: 0 0 0 3px;
  }
}

.cooperation__table .table__cell:last-child {
  @include vp-1023 {
    padding-right: 0;
  }
}

.table__cell {
  text-align: left;
}

.table__cell .table__btn {
  display: flex;
  flex-direction: column;
  padding: 0;
  text-align: left;
  background-color: transparent;
  color: $color-default-white;
  border: none;

  @include vp-767 {
    padding: 0 1px;
    transform: translateY(3px);
    cursor: pointer;

    &--curent {
      border-bottom: 2px solid $yellow-green;
    }
  }
}

.table__title {
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  color: $color-default-white;
  opacity: 0.65;

  @include vp-1023 {
    font-size: 12px;
    line-height: 28px;
    transform: translateY(-3px);
  }

  @include vp-767 {
    display: block;
    line-height: 18px;
    max-width: 150px;
    transition: all $trans-default;
  }
}

.table__text {
  font-weight: 700;
  font-size: 14px;
  line-height: 21px;
  color: $color-default-white;

  @include vp-1023 {
    line-height: 18px;
    transform: translateY(-6px);
  }

  @include vp-767 {
    display: block;
    font-size: 12px;
    white-space: nowrap;
    transform: translateY(-5px);
    max-width: 150px;
    transition: all $trans-default;
  }
}

@media (hover: hover), screen and (min-width: 0\0) {
  .table__btn:hover .table__title,
  .table__btn:hover .table__text {
    @include vp-767 {
      color: $color-white-transparent6;
    }
  }
}


// ----------------------Table-End---------------------------------


.transparent-benefits {
  @include vp-1023 {
    transform: translateY(-11px);
  }

  @include vp-767 {
    transform: translateY(0);
    padding: 25px 15px 0 14px;
  }
}

.transparent-benefits__title {
  margin-bottom: 48px;

  @include vp-1279 {
    margin-bottom: 42px;
  }

  @include vp-1023 {
    margin-bottom: 27px;
  }

  @include vp-767 {
    text-align: left;
    padding-right: 70px;
    padding-left: 1px;
  }
}

.transparent-benefits__list {
  margin: 0 auto;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(3, auto);

  @include vp-1023 {
    width: max-content;
  }

  @include vp-767 {
    width: auto;
    grid-template-columns: auto;
    gap: 10px;
    padding-left: 1px;
  }
}

.transparent-benefits__list .card-item {
  position: relative;
  display: flex;
  flex-direction: column;
  border-radius: 0;
  border-top-left-radius: 30px;
  width: 395px;
  min-height: 370px;
  padding: 0 62px 42px 36px;

  &--sn {
    background: $dark-slate-blue-gradient;
  }

  &--dn {
    background: $free-speech-blue-gradient;
  }

  &--dng {
    background: $slate-blue-gradient;
  }

  &:nth-child(3n + 3) {
    border-top-right-radius: 20px;
  }

  @include vp-1279 {
    width: 282px;
    min-height: 305px;
    border-top-left-radius: 20px;
    padding: 0 25px 30px 30px;
  }

  @include vp-1023 {
    width: 222px;
    padding: 0 28px 30px 25px;

    &--sn {
      background: $dark-slate-blue-gradient2;
    }

    &--dn {
      background: $free-speech-blue-gradient2;
    }

    &--dng {
      background: $slate-blue-gradient2;
      padding-right: 24px;
    }
  }

  @include vp-767 {
    width: 100%;
    min-height: auto;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    padding: 0 28px 2px 20px;

    &--sn {
      background: $dark-slate-blue-gradient3;
    }

    &--dn {
      background: $free-speech-blue-gradient3;
      padding-right: 24px;
      padding-bottom: 3px;
    }

    &--dng {
      background: $slate-blue-gradient3;
    }
  }
}

.card-item--sn br,
.card-item--dn br {
  @include vp-1023 {
    display: none;
  }
}

.card-item--sn br {
  @include vp-767 {
    display: block;
  }
}

.transparent-benefits__list .title {
  margin-bottom: 12px;
  font-size: 24px;
  line-height: 36px;
  letter-spacing: 0.02em;
  order: -1;
  text-align: left;

  &--sn,
  &--dn,
  &--dng {
    margin: 0 0 31px -36px;
    letter-spacing: 0;
    text-transform: uppercase;
    font-size: 16px;
    line-height: 24px;
    min-width: 132px;
    width: max-content;
    padding: 13px 31px 9px 36px;
    border-bottom-right-radius: 30px;
    border-top-left-radius: 30px;
  }

  &--sn {
    background: $blue-to-orchid;
  }

  &--dn {
    background: $aquamarine-to-blue;
  }

  &--dng {
    background: $rajah-to-rose;
  }

  @include vp-1279 {
    font-size: 18px;
    line-height: 27px;
    margin-bottom: 6px;

    &--sn,
    &--dn,
    &--dng {
      min-width: 120px;
      margin: 0 0 23px -30px;
      padding: 12px 15px 10px 27px;
      font-size: 16px;
      line-height: 24px;
