.main-nav {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 2480px;
  width: 100%;
  margin: 0 auto;

  @include vp-1279 {
    display: flex;
  }
}

.main-nav__overlay {
  display: none;
  width: 100%;

  @include vp-1279 {
    display: block;
    height: 100%;
    position: fixed;
    top: 60px;
    left: 0;
    transform: translateX(-1280px);
    transition: all $trans-default;
    background-color: $color-default-white;
    background-image: linear-gradient($color-transparent-2, $color-transparent-2);
  }

  @include vp-767 {
    top: 50px;
    background-image: none;
    background-color: $color-sapphire-3;
  }
}

.main-nav--open .main-nav__overlay {
  @include vp-1279 {
    transform: translateX(0);
  }
}

.main-nav__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 0 0 40px;

  @include vp-767 {
    display: flex;
    position: fixed;
    top: 50px;
    left: 0;
    z-index: 100;
    padding: 0;
    width: 0;
    height: 100%;
    overflow: auto;
    flex-direction: column;
    justify-content: flex-start;
    transition: all $trans-default;
    transform: translateX(-480px);
  }
}

.main-nav--open .main-nav__wrapper {
  @include vp-767 {
    width: 100%;
    transform: translateX(0);
  }
}

.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  margin-right: 20px;
  padding: 0;
  list-style: none;

  @include vp-1279 {
    display: block;
    position: fixed;
    flex-direction: column;
    align-self: flex-start;
    top: 60px;
    left: 0;
    width: 0;
    height: 100%;
    margin: 0;
    padding: 54px 72px;
    transition: all $trans-default;
    transform: translateX(-480px);
    background-color: $color-sapphire-3;
  }

  @include vp-767 {
    position: relative;
    top: 0;
    padding-bottom: 0;
    width: 0;
    height: max-content;
    margin: 0 auto;
    transform: translateX(-480px);
  }
}

.main-nav--open .main-nav__list {
  @include vp-1279 {
    overflow: auto;
    width: 320px;
    transform: translateX(0);
    box-shadow: 0 20px 40px $color-black-transparent-2;
    z-index: 100;
  }

  @include vp-767 {
    overflow: visible;
    width: 100%;
    transform: translateX(0);
  }
}

.main-nav__item {
  margin-right: 20px;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  letter-spacing: 0.06px;

  @include vp-1279 {
    margin: 0;
    padding: 0 0 11px;
    text-align: center;

    &:not(:last-child) {
      margin-bottom: 17px;
      border-bottom: 1px solid $color-white-transparent;
    }
  }
}

.main-nav__link {
  color: $color-default-white;
  outline: none;
  transition: opacity $trans-600;

  &.is-active {
    opacity: 0.6;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      opacity: 0.6;
      transition: opacity $trans-600;
    }
  }
}

.main-nav__toggle {
  display: none;
  outline: none;
  transition: opacity $trans-600;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      opacity: 0.6;
      transition: opacity $trans-600;
    }
  }

  @include vp-1279 {
    display: block;
    position: absolute;
    top: 6px;
    left: -56px;
    width: 30px;
    height: 30px;
    background-color: transparent;
    border: none;
    cursor: pointer;
  }

  @include vp-767 {
    left: 11px;
    top: 10px;
  }
}

.main-nav__toggle--nojs {
  display: none;

  @include vp-1279 {
    display: none;
  }
}

.burger {
  position: absolute;
  top: 8px;
  left: 4px;
  display: block;
  width: 22px;
  height: 2px;
  background-color: $color-default-white;
  border-radius: 5px;
  transition: all $trans-default;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 6px;
    left: 0;
    width: 22px;
    height: 2px;
    background-color: $color-default-white;
    border-radius: 5px;
  }

  &::before {
    transition: all $trans-default;
  }

  &::after {
    top: 12px;
    width: 18px;
    transition: all $trans-default;
  }
}

.main-nav--open .burger {
  top: 14px;
  left: 4px;
  transform: rotate(45deg);

  &::before {
    top: 0;
    transform: rotate(-90deg);
  }

  &::after {
    top: 0;
    opacity: 0;
  }
}

.main-nav__btn-wrapper {
  display: flex;

  @include vp-1279 {
    position: absolute;
    top: 1px;
    right: 108px;
  }

  @include vp-767 {
    position: relative;
    top: auto;
    right: auto;
    z-index: 100;
    display: none;
    width: 100%;
    padding: 51px 0 100px;
    background-color: $color-sapphire-3;
  }
}

.main-nav--open .main-nav__btn-wrapper {
  @include vp-767 {
    display: flex;
    justify-content: center;
  }
}
