.card-item {
  padding: 30px 29px 30px 35px;

  border-radius: 30px;

  &--transparent {
    background: linear-gradient(97.32deg, $color-white-transparent7 -193.6%, $color-transparent 65.18%);
  }

  .card-item__text {
    margin: 0;

    font-weight: 500;
  }

  .title--h4 + .title--h4 {
    position: relative;

    padding-top: 13px;

    @include vp-1023 {
      padding-top: 21px;
    }

    @include vp-767 {
      padding-top: 17px;
    }

    &::after {
      content: "";
      position: absolute;
      top: 4px;
      left: 0;

      width: 100%;
      max-width: 297px;
      height: 1px;
      background: linear-gradient(90.04deg, $color-default-white 0.01%, $color-transparent 80.32%);
      border-radius: 32px;
      opacity: 0.1;

      @include vp-1023 {
        top: 7px;
      }
    }
  }
}
