.footer {
  padding: 86px 0 61px;

  background-color: $color-sapphire;

  @include vp-1439 {
    padding: 50px 0 61px;
  }

  @include vp-1279 {
    padding: 86px 0 48px;
  }

  @include vp-1023 {
    padding: 25px 0 48px;
  }

  @include vp-767 {
    padding: 28px 0 33px;
  }

  &__container {
    display: flex;
    align-items: flex-start;

    @include vp-767 {
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
  }

  &__wrap {
    max-width: 200px;
    margin-right: 50px;

    @include vp-1023 {
      margin-right: 60px;
    }

    @include vp-767 {
      min-width: 100%;
      margin-right: 0;

      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }

  &__date {
    margin-bottom: 25px;

    display: flex;
    align-items: center;

    @include vp-1023 {
      margin-bottom: 21px;
    }

    @include vp-767 {
      margin-bottom: 16px;

      flex-direction: column;
      justify-content: center;
    }
  }

  &__logo {
    width: 44px;
    height: 47px;
    margin-right: 21px;

    @include vp-1023 {
      margin-right: 18px;
      width: 36px;
      height: 39px;
    }

    @include vp-767 {
      width: 38px;
      height: 40px;
      margin: 0 0 6px 0;
    }
  }

  &__privacy {
    width: 150px;

    display: flex;
    flex-direction: column;

    @include vp-767 {
      align-items: center;
    }
  }

  &__copyright {
    font-family: $ff-poppins;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;

    color: $color-default-white;
    opacity: 0.4;

    @include vp-1023 {
      font-size: 12px;
    }
  }

  &__terms-of-use {
    font-family: $ff-poppins;
    font-weight: 500;
    font-size: 12px;
    line-height: 17px;

    color: $color-default-white;
    opacity: 0.7;

    transition: opacity $trans-default;

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        color: $color-default-white;
        opacity: 1;
      }
    }
  }

  &__wrap-menu {
    max-width: 705px;
    margin-left: auto;

    flex-grow: 1;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    @include vp-1279 {
      max-width: 510px;
    }

    @include vp-767 {
      max-width: 100%;
      margin: 20px 0 0;
      flex-grow: 0;

      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }

  &__menu {
    @include vp-767 {
      margin-bottom: 30px;
    }
  }
}
