
.create {
  position: relative;

  padding-top: 105px;
  padding-bottom: 130px;

  color: $color-default-white;

  background: $color-sapphire-4;

  @include vp-1439 {
    padding-top: 74px;
    padding-bottom: 100px;
  }

  @include vp-1279 {
    padding-bottom: 85px;
  }

  @include vp-1023 {
    padding-top: 68px;
  }

  @include vp-767 {
    padding-top: 24px;
    padding-bottom: 20px;
  }

  &__assets-bg {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    max-width: 1440px;
    margin: 0 auto;

    @include vp-1279 {
      height: 400px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__assets-list {
    position: relative;

    display: grid;
    margin: 0;
    margin-bottom: 83px;
    padding: 0;

    list-style: none;

    grid-gap: 20px;
    grid-template-columns: repeat(3, 1fr);
    z-index: 1;

    @include vp-1439 {
      margin-bottom: 78px;
    }

    @include vp-1279 {
      margin-bottom: 56px;
    }

    @include vp-1023 {
      margin-bottom: 51px;
      grid-gap: 21px 19px;
    }

    @include vp-767 {
      margin-bottom: 12px;

      grid-gap: 10px;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: 1fr 1fr;
    }
  }

  .card-item {
    position: relative;
    border-radius: 30px;
    background: linear-gradient(97.32deg, $color-white-transparent7 -193.6%, $color-transparent 65.18%);
    padding: 30px 29px 28px 35px;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: inherit;
      border-style: solid;
      border-width: 1px;
      border-color: transparent;
      mask:
        linear-gradient($color-default-white, $color-default-white) padding-box,
        linear-gradient($color-default-white, $color-default-white);
      mask-composite: exclude;
      background: linear-gradient(97.32deg, $color-white-transparent7 -193.6%, $color-transparent 65.18%) border-box;
    }

    min-height: 215px;

    @include vp-1279 {
      padding: 26px 29px 31px 30px;
    }

    @include vp-1023 {
      min-height: 200px;
      padding: 25px 20px 25px 30px;
    }

    @include vp-767 {
      min-height: 145px;
      padding: 20px 5px 17px 20px;

      border-radius: 20px;

      &:nth-child(3n+1) {
        grid-column: 1/3;
      }
    }

    svg {
      width: 52px;
      height: 52px;
      margin-bottom: 14px;

      @include vp-1279 {
        margin-bottom: 13px;
      }

      @include vp-1023 {
        width: 42px;
        height: 42px;
        margin-bottom: 4px;
      }

      @include vp-767 {
        width: 34px;
        height: 34px;
        margin-bottom: 1px;
      }
    }
  }

  .title--h4 {
    margin-bottom: 5px;
    text-align: left;

    @include vp-1279 {
      width: 100%;
      max-width: 190px;
    }

    @include vp-1023 {
      max-width: 156px;
    }

    @include vp-767 {
      max-width: 100%;
    }
  }

  .card-item__text {
    line-height: 25px;

    opacity: 0.6;

    @include vp-1279 {
      padding-bottom: 8px;

      font-size: 14px;
      line-height: 21px;
    }

    @include vp-1023 {
      padding-bottom: 0;

      font-size: 12px;
      line-height: 20px;
    }

    @include vp-767 {
      width: 100%;
      // max-width: 243px;
    }
  }

  &__level {
    position: relative;
  }

  &__level-img {
    position: absolute;
    top: -85px;
    right: 0;
    left: 0;
    height: 631px;
    width: 100%;
    max-width: 1440px;
    margin: 0 auto;

    @include vp-1439 {
      top: -115px;
    }

    @include vp-1023 {
      top: -62px;
      height: 630px;
    }

    @include vp-767 {
      display: none;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .title--h3 {
    max-width: 801px;
    margin-bottom: 47px;

    @include vp-1439 {
      // margin-bottom: 47px;
    }

    @include vp-1279 {
      margin-bottom: 31px;
    }

    @include vp-767 {
      margin-top: 45px;
    }

    @include vp-619 {
      margin-bottom: 13px;
      margin-top: 3px;
    }

    @include vp-479 {
      max-width: 267px;
      text-align: left;
    }
  }

  &__level-list {
    position: relative;

    display: grid;
    margin: 0;
    padding: 0;

    list-style: none;

    grid-gap: 30px;
    grid-template-columns: repeat(2, 1fr);

    @include vp-1279 {
      grid-gap: 20px;
    }

    @include vp-619 {
      // margin-bottom: 20px;

      grid-gap: 17px;
      grid-template-columns: 1fr;
    }

    .card-list {
      position: relative;
      min-height: 270px;
      padding: 32px 41px;
      background: $color-white-transparent2;
      border-width: 1px;
      backdrop-filter: blur(15px);

      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: inherit;
        border-style: solid;
        border-width: 1px;
        border-color: transparent;
        mask:
          linear-gradient($color-default-white, $color-default-white) padding-box,
          linear-gradient($color-default-white, $color-default-white);
        mask-composite: exclude;
        background: linear-gradient(89.01deg, $color-white-transparent8 -150.7%, $color-transparent 92.06%) border-box;
      }

      @include vp-1439 {
        padding: 35px 31px 31px 41px;
      }

      @include vp-1279 {
        padding: 31px 33px 40px 40px;
      }

      @include vp-1023 {
        padding: 25px 30px 30px;
      }

      @include vp-619 {
        min-height: 207px;
        padding: 0;

        background: $color-sapphire-4;
        border: none;

        &::before {
          display: none;
        }
      }
    }

    .card-list__text {
      width: 100%;
      max-width: 468px;
      margin: 0;
      margin-bottom: 24px;

      font-weight: 500;
      line-height: 29px;

      @include vp-1279 {
        max-width: 340px;
        margin-bottom: 19px;
      }

      @include vp-1023 {
        max-width: 260px;
        margin-bottom: 16px;

        font-size: 14px;
        line-height: 25px;
      }

      @include vp-619 {
        max-width: 100%;
        margin-bottom: 10px;

        font-size: 12px;
        line-height: 20px;
      }
    }

    .card-list__sublist {
      display: grid;
      grid-auto-flow: column dense;
      font-weight: 500;
      grid-template: repeat(3, 1fr) / 191px 200px;
      grid-gap: 10px 25px;

      @include vp-1279 {
        font-size: 14px;
        line-height: 21px;

        grid-gap: 6px;
        grid-template-columns: 152px 175px;
      }

      @include vp-1023 {
        font-size: 14px;
        line-height: 21px;
        grid-auto-flow: row;
        grid-template-columns: 1fr;
        grid-gap: 5px;
      }

      @include vp-619 {
        font-size: 12px;
        line-height: 18px;

        grid-gap: 5px;
      }
    }

    .card-list__sublist-item {
      display: flex;
      position: relative;
      padding-left: 20px;

      @include vp-619 {
        padding-left: 15px;
      }

      svg {
        position: absolute;
        left: 0;
        top: 7px;
        width: 7px;
        height: 7px;
        object-fit: cover;

        @include vp-619 {
          width: 5px;
          height: 5px;
        }
      }
    }
  }
}
