.footer-menu {
  min-width: 260px;

  @include vp-1023 {
    min-width: 165px;
  }

  @include vp-767 {
    min-width: 100%;
  }

  &__wrap {
    margin-top: 12px;

    display: flex;
    justify-content: space-between;

    @include vp-767 {
      padding: 0 10px;
    }
  }

  &__list {
    margin: 0;
    padding: 0;

    column-count: 2;
    column-width: 120px;
    column-gap: 40px;

    list-style: none;

    @include vp-1279 {
      column-width: 115px;
      column-gap: 28px;
    }

    @include vp-1023 {
      column-width: 90px;
      column-gap: 10px;
    }

    @include vp-767 {
      padding-left: 13px;

      column-width: 85px;
      column-gap: 25px;
    }
  }

  &__item {
    max-width: 120px;
    padding-right: 5px;
    margin-bottom: 5px;

    font-family: $ff-poppins;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;

    @include vp-1023 {
      max-width: 75px;
      font-size: 12px;
      line-height: 18px;
    }

    @include vp-767 {
      padding-right: 0;
      text-align: center;
    }
  }

  &__link {
    display: block;

    color: $color-default-white;
    opacity: 0.7;

    transition: opacity $trans-default;

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        color: $color-default-white;
        opacity: 1;
      }
    }
  }
}
