.media {
  margin: 98px 0;

  @include vp-1439 {
    margin: 58px 0 78px;
  }

  @include vp-1279 {
    margin: 40px 0 71px;
  }

  @include vp-1023 {
    margin: 23px 0 61px;
  }

  @include vp-767 {
    margin: 0 0 25px;
  }
}

.media__wrapper {
  position: relative;
  background-color: $color-alice-blue;
  border-radius: 50px;
  height: 680px;
  padding: 51px 0 48px;
  overflow: hidden;

  &:hover .media__list {
    animation-play-state: paused;
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 142px;
    left: 0;
    display: block;
    width: 170px;
    height: 490px;
    background: $light-gradient;
    z-index: 100;
  }

  &::after {
    left: auto;
    right: 0;
    background: $light-gradient;
    transform: matrix(-1, 0, 0, 1, 0, 0);
  }

  @include vp-1279 {
    height: 600px;
    padding-bottom: 43px;

    &::before,
    &::after {
      top: 147px;
      left: -30px;
      width: 143px;
      height: 410px;
    }

    &::after {
      left: auto;
      right: -30px;
    }
  }

  @include vp-1023 {
    height: 500px;
    padding: 44px 0 48px;

    &::before,
    &::after {
      top: 128px;
      width: 113px;
      height: 324px;
    }
  }

  @include vp-767 {
    height: 555px;
    padding: 15px 15px 21px;
    border-radius: 20px;

    &::before,
    &::after {
      display: none;
    }
  }
}

.media .media__title {
  color: $color-sapphire;
  display: flex;
  flex-direction: column;
  justify-content: center;

  @include vp-767 {
    text-align: start;
    margin-bottom: 15px;
  }
}

.media__title span {
  margin-top: 4px;
  font-weight: 700;
  font-size: 20px;
  line-height: 30px;

  @include vp-1023 {
    margin: 2px 0 0;
    font-size: 18px;
    line-height: 27px;
  }

  @include vp-767 {
    margin: 0;
    font-size: 14px;
    line-height: 21px;
  }
}

.media__wrapper img {
  position: absolute;
  top: -254px;
  left: -169px;
  min-width: 1407px;
  object-fit: cover;
  transform: rotate(27.83deg);
  opacity: 0.02;

  @include vp-1279 {
    left: -335px;
  }

  @include vp-1023 {
    left: -425px;
  }

  @include vp-767 {
    width: 922px;
    height: 985px;
    top: -100px;
    left: -558px;
    object-fit: contain;
  }
}
