.joined {
  position: relative;
  padding-top: 87px;
  padding-bottom: 110px;
  background-color: $color-sapphire-4;

  @include vp-1439 {
    padding-top: 46px;
    padding-bottom: 81px;
  }

  @include vp-1279 {
    padding-bottom: 73px;
  }

  @include vp-767 {
    padding-top: 20px;
    padding-bottom: 30px;
  }

  &__img {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    height: 200px;
    width: 100%;
    max-width: 1440px;
    margin: 0 auto;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }


  &__list {
    position: relative;
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(4, 1fr);
    margin: 0;
    padding: 0;
    list-style: none;

    @include vp-1279 {
      grid-gap: 15px;
    }

    @include vp-767 {
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 10px;
    }
  }

  .title--h2 {
    margin-bottom: 30px;
    font-weight: 700;
    font-size: 28px;
    line-height: 42px;

    @include vp-1279 {
      margin-bottom: 26px;
    }

    @include vp-1023 {
      font-size: 24px;
      line-height: 36px;
    }

    @include vp-767 {
      margin-bottom: 11px;
      font-size: 20px;
      line-height: 29px;
    }
  }

  .card-item {
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 20px;
    padding: 36px 55px;

    @include vp-1279 {
      padding: 32px 28px;
    }

    @include vp-1023 {
      padding: 27px 22px;
    }

    @include vp-767 {
      min-height: 80px;
      padding: 21px 15px;
      border-radius: 10px;
    }
  }

  .card-item--pokerstars svg {
    @include vp-1279 {
      width: 142px;
      height: 24px;
    }

    @include vp-1023 {
      width: 110px;
      height: 18px;
    }
  }

  .card-item--truelab svg {
    @include vp-1023 {
      width: 110px;
      height: 18px;
    }
  }

  .card-item--fashion svg {
    @include vp-1023 {
      width: 103px;
      height: 23px;
    }
  }

  .card-item--cryptobots svg {
    @include vp-1023 {
      width: 38px;
      height: 38px;
    }
  }
}
