.nft-card {
  position: relative;

  max-width: 570px;
  min-height: 530px;
  padding: 30px;

  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  border-radius: 30px;

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;

    width: 100%;
    height: 100%;

    background: linear-gradient(180deg, rgba(0, 26, 84, 0.7) 23%, transparent 0.8 100%);

    @include vp-767 {
      background: linear-gradient(360deg, rgba(0, 26, 84, 0.7) 23%, transparent 0.8 100%);
    }
  }

  @include vp-1279 {
    max-width: 413px;
    min-height: 450px;
    padding: 26px 64px;
  }

  @include vp-1023 {
    max-width: 324px;
    min-height: 350px;
    padding: 26px 30px;
  }

  @include vp-767 {
    max-width: 290px;
    min-height: 250px;
    padding: 13px 15px;

    justify-content: flex-end;
  }

  &__title {
    margin-bottom: 5px;

    font-weight: 800;
    font-size: 28px;
    line-height: 42px;

    @include vp-1023 {
      font-size: 24px;
      line-height: 36px;
    }

    @include vp-767 {
      order: 1;
      margin-bottom: 2px;

      font-size: 16px;
      line-height: 24px;

      text-align: left;
    }
  }

  &__text {
    max-width: 400px;
    margin: 0 auto 20px;

    font-weight: 500;
    font-size: 16px;
    line-height: 26px;

    text-align: center;

    @include vp-1023 {
      margin: 0;

      font-size: 14px;
      line-height: 22px;
    }

    @include vp-767 {
      order: 2;
      margin: 0;

      font-size: 12px;
      line-height: 20px;

      text-align: left;
    }
  }

  img {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;

    width: 100%;
    height: 100%;

    object-fit: cover;
    border-radius: 30px;
  }

  &--dinamic img:last-child {
    position: relative;
    left: 48%;
    transform: translateX(-50%);
    z-index: 20;

    max-width: 85%;
    min-height: 90%;

    object-fit: cover;

    @include vp-1279 {
      max-width: 100%;
      min-height: 75%;
    }

    @include vp-1023 {
      max-width: 90%;
      min-height: 82%;
    }

    @include vp-767 {
      order: 0;
      top: 29px;

      max-width: 67%;
      min-height: 77%;
    }
  }
}
