.guide {
  position: relative;
  padding-top: 106px;
  padding-bottom: 115px;

  color: $color-default-white;

  background: $color-sapphire;

  @include vp-1919 {
    padding-top: 105px;
  }

  @include vp-1439 {
    padding-bottom: 84px;
    padding-top: 74px;
  }

  @include vp-1279 {
    padding-top: 60px;
    padding-bottom: 63px;
  }

  @include vp-1023 {
    padding-top: 60px;
    padding-bottom: 55px;
  }

  @include vp-619 {
    padding-top: 18px;
    padding-bottom: 19px;
  }

  &__bg {
    position: absolute;
    max-width: 1440px;
    margin: 0 auto;
    left: 0;
    right: 0;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__bg--top {
    height: 154px;
    top: 0;

    @include vp-767 {
      height: 117px;
    }
  }

  &__bg--bottom {
    bottom: 0;

    @include vp-1439 {
      height: 130px;
    }

    @include vp-767 {
      height: 100px;
    }
  }

  .title--h2 {
    width: 100%;
    max-width: 791px;
    padding-bottom: 44px;

    @include vp-1023 {
      padding-bottom: 30px;
    }

    @include vp-767 {
      line-height: 30px;
      padding: 0 15px 16px;
      text-align: left;
    }
  }

  &__highlighted-title {
    display: inline;

    background: $razzle-dazzle-rose-to-maya-blue;
    background-clip: text;

    -webkit-text-fill-color: transparent;
  }

  &__list {
    max-width: 1180px;
    width: 100%;
    display: grid;
    margin: 0;
    margin-bottom: 65px;
    padding: 0;

    list-style: none;

    grid-gap: 20px;
    grid-template-columns: repeat(3, 1fr);

    @include vp-1279 {
      margin-bottom: 27px;
    }

    @include vp-1023 {
      margin-bottom: 2px;
    }

    @include vp-767 {
      margin-bottom: 15px;
      padding: 0 15px;

      grid-gap: 5px;
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__highlighted-item {
    display: inline;

    position: relative;

    background: $razzle-dazzle-rose-to-maya-blue2;

    background-clip: text;

    -webkit-text-fill-color: transparent;
    -webkit-box-decoration-break: clone;
  }

  .card {
    padding-right: 38px;

    @include vp-1279 {
      padding: 27px 33px 36px 27px;
    }

    @include vp-1023 {
      padding-right: 29px;
    }

    @include vp-767 {
      padding: 14px 20px 16px;
      border-radius: 20px;
    }
  }

  .card__text {
    display: inline-block;
    font-weight: 500;
    font-size: 20px;
    line-height: 34px;

    @include vp-1279 {
      font-size: 18px;
      line-height: 30px;
    }

    @include vp-1023 {
      font-size: 14px;
      line-height: 25px;
    }

    @include vp-767 {
      font-size: 12px;
      line-height: 20px;
    }
  }

  &__item {
    @include vp-1279 {
      padding: 27px 32px 22px 28px;
    }

    @include vp-1023 {
      padding: 27px 32px 22px 28px;
    }

    @include vp-767 {
      padding: 14px 20px 16px;
    }
  }
}
