.meta-intro {
  position: relative;
  padding: 106px 0 112px;

  @include vp-1439 {
    padding: 74px 0 91px;
  }

  @include vp-1279 {
    padding: 74px 0 65px;
  }

  @include vp-1023 {
    padding: 57px 0 61px;
  }

  @include vp-767 {
    padding: 18px 0 20px;
  }

  &__wrap {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;

    width: 100%;
    max-width: 1600px;
    height: 770px;
    margin: 0 auto;

    @include vp-1023 {
      height: 560px;
    }

    @include vp-767 {
      height: 634px;
    }

    img {
      width: 100%;
      height: 100%;

      object-fit: cover;
    }
  }

  &__title {
    margin-bottom: 49px;

    @include vp-1279 {
      margin-bottom: 33px;
    }

    @include vp-1023 {
      margin-bottom: 21px;

      line-height: 54px;
    }

    @include vp-767 {
      margin-bottom: 10px;

      text-align: left;
    }
  }

  &__title-gradient {
    background: $intro-gradient;
    background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  &__wrapper {
    margin: 0 auto;
    padding: 0 0 0 50px;

    display: grid;
    grid-template-columns: auto 1fr;
    gap: 0 50px;
    align-items: center;

    @include vp-1279 {
      padding: 0;
      gap: 0 40px;
    }

    @include vp-1023 {
      gap: 30px;
    }

    @include vp-767 {
      display: flex;
      flex-direction: column;
      gap: 14px;
    }
  }

  &__img {
    position: relative;

    width: 595px;
    height: 461px;

    @include vp-1279 {
      width: 410px;
      height: 317px;
    }

    @include vp-1023 {
      width: 295px;
      height: 228px;
      margin-top: -45px;
    }

    @include vp-767 {
      width: 290px;
      height: 224px;
      margin: 0;
    }

    img {
      width: 100%;
      height: 100%;

      object-fit: cover;
    }
  }

  &__text {
    padding: 5px 27px 10px 0;

    font-weight: 600;
    font-size: 20px;
    line-height: 34px;

    @include vp-1279 {
      padding: 5px 0 10px 0;

      font-size: 18px;
      line-height: 30px;
    }

    @include vp-1023 {
      padding: 0;

      font-size: 16px;
      line-height: 28px;
    }

    @include vp-767 {
      font-size: 14px;
      line-height: 24px;
    }

    p {
      margin: 0 0 15px;

      @include vp-767 {
        margin: 0 0 10px;
      }
    }

    p:last-of-type {
      margin: 0;
    }
  }
}
