.card {
  padding: 37px 27px 38px 38px;

  border-radius: 30px;

  &--transparent {
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0) 100%);
  }

  &__title {
    margin: 0;

    font-weight: 800;
    font-size: 22px;
    line-height: 33px;
  }

  &__text {
    margin: 0;

    font-weight: 500;
    font-size: 16px;
    line-height: 28px;
  }
}
