.intro {
  margin-bottom: 127px;

  @include vp-1439 {
    margin-bottom: 110px;
  }

  @include vp-1279 {
    margin-bottom: 99px;
  }

  @include vp-1023 {
    margin-bottom: 77px;
  }

  @include vp-767 {
    margin-bottom: 24px;
  }

  &__title {
    margin-bottom: 8px;

    line-height: 69px;

    @include vp-1023 {
      line-height: 54px;
    }

    @include vp-767 {
      margin-bottom: 10px;

      text-align: left;
    }
  }

  &__title-gradient {
    background: $intro-gradient;
    background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  &__description {
    max-width: 840px;
    margin: 0 auto 55px;
    padding: 0 20px;

    font-weight: 600;
    font-size: 20px;
    line-height: 34px;

    text-align: center;

    @include vp-1279 {
      margin: 0 auto 55px;
    }

    @include vp-1023 {
      margin: 0 auto 37px;
      padding: 0;

      font-size: 18px;
      line-height: 30px;
    }

    @include vp-767 {
      margin: 0 auto 16px;
      padding-right: 10px;

      font-size: 14px;
      line-height: 24px;

      text-align: left;
    }
  }

  &__list {
    margin: 0 0 86px 0;
    padding: 0;

    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(auto-fit, auto);
    gap: 40px;
    justify-content: center;

    list-style: none;

    @include vp-1279 {
      margin: 0 0 69px;

      gap: 20px;
      justify-items: center;
    }

    @include vp-1023 {
      margin: 0 0 60px 0;
    }

    @include vp-767 {
      margin: 0 0 23px 0;

      grid-template-columns: repeat(auto-fit, 290px);
      gap: 10px;
    }
  }
}
