.promo-site {
  position: relative;
  max-width: 1180px;
  padding: 48px 15px 56px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  background: linear-gradient(180deg, rgba(255, 255, 255, 0.1) -66.25%, rgba(255, 255, 255, 0) 181.43%);
  border-radius: 30px;

  @include vp-1279 {
    padding: 40px 15px 46px;
  }

  @include vp-1023 {
    padding: 34px 15px 38px;
  }

  @include vp-767 {
    padding: 21px 20px 25px;
  }

  img {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: -1;

    width: 100%;
    height: 100%;

    object-fit: cover;
    border-radius: 30px;
  }

  &__title {


    margin-bottom: 5px;

    line-height: 54px;

    @include vp-1279 {
      margin-bottom: 3px;

      font-size: 28px;
      line-height: 42px;
    }

    @include vp-1023 {

      margin-bottom: 8px;

      font-size: 22px;
      line-height: 33px;
    }

    @include vp-767 {
      margin-bottom: 7px;

      font-size: 18px;
      line-height: 27px;
    }
  }

  &__text {


    margin: 0 0 27px 0;


    font-weight: 600;
    font-size: 20px;
    line-height: 30px;

    text-align: center;

    @include vp-1279 {
      margin: 0 0 22px 0;

      font-size: 18px;
      line-height: 27px;
    }

    @include vp-1023 {

      margin: 0 0 19px 0;
      padding: 0 57px;

      font-size: 16px;
      line-height: 24px;
    }

    @include vp-767 {
      margin: 0 0 13px 0;
      padding: 0;

      font-size: 14px;
      line-height: 20px;
    }
  }
}
