.form {
  overflow: hidden;
  position: relative;
  // padding-top: 88px;

  padding-bottom: 123px;

  @include vp-767 {
    padding-bottom: 0;
  }

  &__bg--bottom {
    position: relative;

    img {
      position: absolute;
      bottom: -123px;
      max-width: 1440px;
      margin: 0 auto;
      left: 0;
      right: 0;
      width: 100%;
      height: 250px;
      object-fit: cover;

      @include vp-767 {
        display: none;
      }
    }
  }

  &__wrapper {
    position: relative;

    img {
      position: absolute;
      width: 176px;
      height: 552px;
      right: -178px;
      top: -50px;

      @include vp-1279 {
        display: none;
      }
    }
  }

  .title--h2 {
    max-width: 616px;
    margin-bottom: 16px;

    background: linear-gradient(264.68deg, $color-razzle-dazzle-rose -10.87%, $color-maya-blue 92.2%),
      linear-gradient(0deg, $color-default-white, $color-default-white);
    background-clip: text;

    -webkit-text-fill-color: transparent;

    @include vp-1023 {
      max-width: 581px;
    }

    @include vp-767 {
      margin-bottom: 4px;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 49px;
    font-weight: 700;
    font-size: 20px;
    line-height: 30px;

    text-align: center;

    @include vp-1023 {
      margin-bottom: 31px;
      font-size: 18px;
      line-height: 30px;
    }

    @include vp-767 {
      margin-bottom: 14px;
      padding: 0 20px;
      font-size: 16px;
      line-height: 24px;
    }
  }

  form {
    position: relative;
    display: grid;
    // grid-template-columns: repeat(2, minmax(274px,340px));
    grid-gap: 20px;
    max-width: 800px;
    width: 100%;
    margin: 0 auto;
    padding: 48px 50px;
    background-color: $color-default-white;
    color: $color-sapphire;
    border-radius: 30px;
    z-index: 1;

    @include vp-1023 {
      max-width: 668px;
    }

    @include vp-767 {
      max-width: 767px;
      padding: 15px 15px 30px;
      grid-template-columns: repeat(1, 1fr);
      grid-gap: 14px 0;
      border-radius: 0;
      background-color: $color-default-white;
    }
  }

  .custom-input:first-child {
    grid-column: 1/3;
  }

  .custom-textarea {
    grid-column: 1/3;

    &__label {
      margin-bottom: 6px;
      font-size: 14px;
      line-height: 20px;

      @include vp-767 {
        font-size: 12px;
        line-height: 17px;
      }
    }

    textarea {
      min-height: 150px;

      @include vp-767 {
        min-height: 110px;
      }
    }
  }

  .custom-input {
    display: flex;
    flex-direction: column;

    &__label {
      margin-bottom: 6px;
    }

    @include vp-767 {
      grid-column: 1;
    }

    label {
      font-size: 14px;
      line-height: 20px;

      @include vp-767 {
        font-size: 12px;
        line-height: 17px;

        input {
          min-height: 45px;
        }
      }
    }
  }

  .custom-input__variant {
    color: rgba(0, 26, 84, 0.4);
    padding-left: 5px;
  }

  .custom-toggle {
    grid-column: 1/3;
    color: rgba(0, 26, 84, 0.6);

    &__label {
      display: inline-block;
      font-size: 12px;
      line-height: 17px;

      a {
        color: $color-royal-blue;
      }

      @include vp-767 {
        font-size: 10px;
        line-height: 14.4px;
      }
    }
  }

  &__btn {
    width: 100%;
    max-width: 106px;
    padding: 14.5px 0;
    background: $color-royal-blue;
    color: $color-default-white;
    border-radius: 100px;
    font-weight: 600;
    font-size: 14px;
    line-height: 21px;
    // cursor: not-allowed;

    &:hover {
      background: $color-royal-blue2;
    }

    &:focus {
      background: $color-royal-blue2;
      box-shadow: inset 0 2px 2px rgba(0, 0, 0, 0.1);
    }

    @include vp-767 {
      max-width: 100%;
      padding: 9.5px 0;
    }
  }
}
