.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  height: max-content;
  margin: 0;
  padding: 0;
  outline: none;

  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  color: $color-default-white;

  border: none;
  border-radius: 100px;
  cursor: pointer;

  transition: opacity $trans-default;

  &:disabled {
    opacity: 0.4;

    pointer-events: none;
  }
}

.btn--transparent {
  font-weight: 500;
  border: 1px solid rgba($color-default-white, 0.2);
  transition: border $trans-600;

  &-disabled {
    border: 1px solid rgba($color-default-white, 0.2);
    opacity: 0.4;
    pointer-events: none;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus,
    &:active {
      border: 1px solid rgba($color-default-white, 0.6);

      transition: border $trans-600;
    }
  }
}

.btn--transparent.btn--main-nav,
.btn--transparent.btn--main-screen {
  min-height: 40px;
  padding: 0 22px;
}

.btn--transparent.btn--main-nav {
  @include vp-767 {
    width: max-content;
  }
}

.btn--transparent.btn--main-screen {
  width: max-content;
  margin: 0 auto 17px;
  padding: 0 22px;
  animation: show 6s forwards;
  opacity: 0;

  @include vp-767 {
    font-size: 13px;
    line-height: 19px;
  }
}

.btn--transparent.btn--footer {
  max-width: 142px;
  padding: 10px;

  font-size: 13px;
  line-height: 19px;

  @include vp-1023 {
    min-width: 136px;

    font-size: 12px;
    line-height: 18px;
  }

  @include vp-767 {
    min-width: 130px;
  }

  svg {
    margin-right: 7px;
  }
}

.btn--light {
  position: relative;
  background: $orange-gradient;
  overflow: hidden;
  z-index: 100;

  &::before {
    content: "";
    display: block;
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: $orange-gradient-2;
    transition: opacity $trans-600;
    z-index: -1000;
  }

  &-disabled {
    background: $orange-gradient;
    opacity: 0.4;
    pointer-events: none;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover::before,
    &:focus::before,
    &:active::before {
      opacity: 1;
      transition: opacity $trans-600;
    }

    &:active::before {
      box-shadow: inset 0 2px 2px $color-black-transparent;
    }
  }
}

.btn--light.btn--main-nav {
  min-height: 40px;
  margin-left: 7px;
  padding: 0 38px;

  @include vp-1279 {
    padding: 0 40px;
  }

  @include vp-767 {
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    min-height: 35px;
    margin-right: 15px;
    padding: 0 30px;
  }
}

.btn--light.btn--main-screen {
  width: max-content;
  min-height: 60px;
  margin: 0 auto 17px;
  padding: 0 63px;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  animation: show 6s forwards;
  opacity: 0;

  @include vp-767 {
    min-height: 50px;
    margin: 0 auto 10px;
    padding: 0 43px;
    font-weight: 600;
    font-size: 14px;
    line-height: 21px;
  }
}

.btn--gradient {
  background-image: $promo-gradient-2;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      background-image: $promo-gradient-3;
    }

    &:focus {
      box-shadow: inset 0 2px 2px $color-black-transparent;
    }
  }
}

.btn--gradient.btn--promo {
  max-width: 197px;
  padding: 18px 45px;

  font-weight: 700;
  font-size: 16px;
  line-height: 24px;

  @include vp-1023 {
    max-width: 163px;
    padding: 14px 30px;

    font-size: 14px;
    line-height: 21px;
  }
}
