.header {
  position: sticky;
  top: 0;
  z-index: 1000;
  background-color: $color-sapphire-2;
  width: 100%;

  .container {
    display: flex;
    width: 100%;
    max-width: 100%;
    min-height: 60px;
    padding: 9px 30px 9px 50px;

    @include vp-1279 {
      padding: 9px 20px 9px 82px;
    }

    @include vp-767 {
      padding: 0;
      min-height: 50px;
    }
  }
}

.header__logo {
  display: block;
  min-width: max-content;
  min-height: max-content;
  outline: none;

  @include vp-767 {
    min-width: auto;
    min-height: auto;
    width: 26px;
    height: 28px;
    margin-left: 62px;
  }
}

.header__logo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: left;
}
